import React, { useState } from 'react';
import '../css/gallery.css';
import '../css/font.css';

const imageFiles = [
  { file: '250709.jpg', title: '', text: '  !' },
  { file: '250710.jpg', title: ' ', text: '  ' },
  { file: '250712.jpg', title: ' ', text: '  ' },
  { file: '250713.jpg', title: ' 4', text: '  <br />  ' },
  { file: '250714.jpg', title: ' 5', text: '  <br />   ' },
  { file: '250715.jpg', title: ' 6', text: '    ' },
  { file: '250717.jpg', title: ' 7', text: '  <br /> ' },
  { file: '250719.jpg', title: ' 8', text: '    ' },
  { file: '250720_1.jpg', title: ' 9', text: '   ' },
  { file: '250720_2.jpg', title: ' 10', text: '    ' },
  { file: '250722.jpg', title: ' 11', text: '  <br />   ' },
  { file: '250724.jpg', title: ' 12', text: '   ' },
  { file: '250725.jpg', title: ' 13', text: '    ' },
  { file: '250725_2.jpg', title: ' 14', text: '  <br /> ~' },
  { file: '250725_3.jpg', title: ' 15', text: '  ' },
  { file: '250727.jpg', title: ' 16', text: '   ' },
  { file: '250728.jpg', title: ' 16', text: '   ' },
  { file: '250731.jpg', title: ' 17', text: '   ' },
  { file: '250731_2.jpg', title: ' 18', text: '  ~' },
  { file: '250801.jpg', title: ' 19', text: '   ' },
  { file: '250801_2.jpg', title: ' 20', text: '   ' },
  { file: '250803.jpg', title: ' 22', text: '  ?!' },
  { file: '250806_1.jpg', title: ' 23', text: '    ' },
  { file: '250806_2.jpg', title: ' 24', text: '   ' },
];

const bgClasses = ['gallery-card1', 'gallery-card2', 'gallery-card3'];

const galleryData = imageFiles.map(({ file, title, text }) => {
  const raw = file.slice(0, 6);
  const year = `20${raw.slice(0, 2)}`;
  const month = raw.slice(2, 4);
  const day = raw.slice(4, 6);
  const date = `${year}-${month}-${day}`;
  const img = require(`../images/gallery/${file}`);

  const jsDate = new Date(`${year}-${month}-${day}`);
  const dayNames = ['', '', '', '', '', '', ''];
  const weekday = dayNames[jsDate.getDay()];
  const dateLabel = `${year} ${parseInt(month)} ${parseInt(day)} (${weekday})`;

  return { title, img, text, date, year, month, dateLabel };
});

const Gallery = () => {
  const [selectedYear, setSelectedYear] = useState('2025');
  const [selectedMonth, setSelectedMonth] = useState('');

  const filteredData = galleryData.filter(item =>
    item.year === selectedYear && (selectedMonth === '' || item.month === selectedMonth)
  );

  return (
    <main className="gallery-page">
      <div className="gallery-header">
        <span className="gallery-icon"></span>
        <h2 className="gallery-title">GALLERY</h2>
      </div>
      <p className="gallery-description">   !</p>

      <div className="gallery-filter">
        <select value={selectedYear} onChange={(e) => setSelectedYear(e.target.value)}>
          <option value="2025">2025</option>
        </select>
        <select value={selectedMonth} onChange={(e) => setSelectedMonth(e.target.value)}>
          <option value=""> </option>
          <option value="07">7</option>
          <option value="08">8</option>
        </select>
      </div>

      <div className="gallery-gallery">
        {filteredData.slice().reverse().map((item, index) => (
          <div key={index} className={`gallery-card ${bgClasses[index % bgClasses.length]}`}>
            <strong className="gallery-title-card">{item.title}</strong>
            <img src={item.img} alt={item.title} className="gallery-image" />
            <div className="gallery-date">{item.dateLabel}</div>
            <div
              className="gallery-text"
              dangerouslySetInnerHTML={{ __html: item.text }}
            />
          </div>
        ))}
      </div>
    </main>
  );
};

export default Gallery;
